import { Box, Typography, useTheme } from "@mui/material";
import Friend from "../../components/Friend";
import WidgetWrapper from "../../components/WidgetsWrapper";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setFriends } from "../../state/index";
import GroupAddOutlinedIcon from '@mui/icons-material/GroupAddOutlined';

const FriendListWidget = ({ userId }) => {
  const dispatch = useDispatch();
  const { palette } = useTheme();
  const token = useSelector((state) => state.token);
  const friends = useSelector((state) => state.user.friends);
  const reRander = useSelector((state)=> state.reRenderPage)

  const getFriends = async () => {
  
    const response = await fetch(
      `https://social-media-app-icnj.onrender.com/auth/users/${userId}/friends`,
      {
        method: "GET",
        headers: { Authorization: `Bearer ${token}` },
      }
    );
 
    const data = await response.json();
      
    dispatch(setFriends({ friends: data }));
  };

  useEffect(() => {
    getFriends();
  }, [ reRander]); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <WidgetWrapper>
      <Typography
        color={palette.neutral.dark}
        variant="h5"
        fontWeight="500"
        sx={{ mb: "1.5rem" }}
      > <div  style={{marginBottom: "-15px", marginTop: "-10px", position: "relative"}} ><GroupAddOutlinedIcon/><span style={{ position: "absolute", top:"0px", paddingLeft: "9px"}}>Friend List</span></div>
        
      </Typography>
      <Box display="flex" flexDirection="column" gap="1.5rem">
        { friends.length &&friends.map((friend) => (
          <Friend
            key={friend._id}
            friendId={friend._id}
            name={`${friend.firstName} ${friend.lastName}`}
            subtitle={friend.occupation}
            userPicturePath={friend.picturePath}
          />
        ))}
      </Box>
    </WidgetWrapper>
  );
};

export default FriendListWidget;