
import React, { useEffect } from 'react'
import Navbar from '../navbar'
import { Box, useMediaQuery } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";

import UserWidget from "../widgets/userWidgets";
import MyPostWidget from '../widgets/myPostWidgets';
import PostsWidget from '../widgets/postsWidgets';
import AdvertWidget from '../widgets/advertWidget';
import FriendListWidget from '../widgets/frindListWidgets';
import { setIsUserProfile } from '../../state';

;


const Homepage = () => {
 
    const dispatch = useDispatch()
   const isNonMobileScreens = useMediaQuery("(min-width:1000px)");
   const { _id, picturePath } = useSelector((state) => state.user);
  dispatch(setIsUserProfile({ isUserProfile :false }))
 

  
  return (
    <Box>
        <Navbar/>
         <Box
        width="100%"
        padding="2rem 6%"
        display={isNonMobileScreens ? "flex" : "block"}
        gap="0.5rem"
        justifyContent="space-between"
      >
        <Box flexBasis={isNonMobileScreens ? "26%" : undefined}>
          <UserWidget userId={_id} picturePath={picturePath} />
        </Box>
        <Box
          flexBasis={isNonMobileScreens ? "42%" : undefined}
          mt={isNonMobileScreens ? undefined : "2rem"}
        >
         <MyPostWidget picturePath={picturePath} />
          <PostsWidget userId={_id} />
        </Box>
         {isNonMobileScreens && (
          <Box flexBasis="26%">
          <AdvertWidget /> 
            <Box m="2rem 0" />
           <FriendListWidget userId={_id} /> 
          </Box>
        )} 
      </Box>
    </Box>
  )
}

export default Homepage